___CSS_LOADER_EXPORT___.push([module.id, `.container {
    display: flex;
}

ul{
    list-style: none;
}

#header_Container{
    display: flex;
    background-color: blue;
    flex: 0 0 100%;
}

.settingsBarItem{
    display: inline-block;
    margin-right: 10px;
}

.toolBar_Container {
    width: 400px; /* Set a width for the toolbar */
    /* Additional toolbar styles */
}

#pixelCanvas_Container { 
    display: flex;
    flex-direction: column; /* Stack children vertically */
    height: 100vh;
    width: 100vw;
    position: relative;
}

.canvasWrapper {
    position: relative;
    flex-grow: 1; /* Allow it to take up the remaining space */
    overflow: hidden; /* Ensure canvases do not overflow */
}

#backgroundCanvas, #pixelCanvas {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
}

#backgroundCanvas {
    z-index: 1;
}

#pixelCanvas {
    z-index: 2;
}

.colorInfoContainer {
    display: flex;
}

.colorBox {
    width: 10px;
    height: 10px;
    border: 4px dotted black;
}

.modal {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    padding-top: 60px;
}

.modal-content {
    background-color: #fefefe;
    margin: 5% auto; /* 15% from the top and centered */
    padding: 20px;
    border: 1px solid #888;
    width: 80%; /* Could be more or less, depending on screen size */
}

.close-button {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
}

.close-button:hover,
.close-button:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
}
